/* EventDetails.css */

.event-details-overlay {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 988;
  overflow: hidden;
  animation: slideIn 0.3s forwards;
}

@keyframes slideIn {
  from {
    transform: translateY(100vh);
  }
  to {
    transform: translateY(0);
  }
}

.event-details-content {
  position: relative;
  width: 70vw;
  background-color: rgba(255, 255, 255, 0.9);
  border: 2px solid rgb(167, 195, 255);
  padding: 0;
  margin: auto;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  animation: slideContent 0.3s forwards;
}

@keyframes slideContent {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.event-details-overlay.closing {
  animation: slideOutOverlay 0.5s forwards;
}

@keyframes slideOutOverlay {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.event-details-content.closing {
  animation: slideOutContent 0.5s forwards;
}

@keyframes slideOutContent {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-100%);
    opacity: 0;
  }
}

.modul-event-name {
  text-align: center;
  align-items: center;
  padding-top: 15px;
  margin: 0;
  padding-bottom: 0;
}

.p-info {
  text-align: left;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0;
}

.close-button {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: rgba(170, 170, 170, 0.5);
  border: 2px solid rgb(167, 195, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: rgb(167, 195, 255);
  cursor: pointer;
  z-index: 999;
}

.close-button:hover {
  color: white;
  border-color: white;
}

.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
}


