/* App.css */

.App {
  text-align: center;

  /* Define text color based on Telegram theme */
  color: var(--tg-theme-text-color);
}

.Header-app {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  /* background-color: #303030; */
  
  /* Use Telegram theme background color */
  background-color: var(--tg-theme-bg-color);

  /* Define text color based on Telegram theme */
  color: var(--tg-theme-text-color);

  /* padding: 10px; */
  z-index: 888;
  overflow: hidden;
}
